<template>
  <div
    class="char"
    :class="css_class"
    ref="char_ref"
    @mouseenter="emit('touch')"
  >
    {{ char }}
  </div>
</template>

<script setup>
import { watch, ref } from "vue";
const props = defineProps({
  char: String,
  ctrl: String,
  show: Boolean,
});
const emit = defineEmits(["touch"]);

const opened = ref(false);
const char_ref = ref(null);

const refresh_css = () => {
  char_ref.value.style.animation = "none";
  char_ref.value.offsetHeight;
  char_ref.value.style.animation = "";
};

const css_class = ref("off-2-none");

watch(
  () => props.show,
  (show) => {
    if (show) {
      let sub = props.ctrl == "1" ? "on" : "off";
      css_class.value = `none-2-${sub}`;
    } else {
      let pre = opened.value ? "on" : "off";
      css_class.value = `${pre}-2-none`;
    }
    opened.value = props.ctrl == "1";
    refresh_css();
  }
);
</script>

<style>
@keyframes showon {
  from {
    background-color: var(--dark-c);
    color: var(--light-c);
  }
  to {
    background-color: var(--light-c);
    color: var(--dark-c);
  }
}

@keyframes showoff {
  from {
    background-color: var(--dark-c);
    color: var(--light-c);
    opacity: 1;
  }
  to {
    background-color: var(--dark-c);
    color: var(--light-c);
    opacity: 0.2;
  }
}

.off-2-none {
  animation: showoff 1s 1 both reverse;
}

.none-2-off {
  animation: showoff 0.5s 1 both;
}

.on-2-none {
  animation: showon 1s 1 both reverse;
}

.none-2-on {
  animation: showon 1s 1 both;
}

.char {
  cursor: default;
  user-select: none;
  padding: 6px min(10px, 2vw);

  --dark-c: #222;
  --light-c: #eee;
}
</style>