<template>
  <BaseChar
    v-for="(t, i) in text"
    :key="i"
    :char="t"
    :ctrl="cs[i]"
    :show="show"
    @touch="emit('touch')"
  />
</template>

<script setup>
import { computed } from "vue";
import BaseChar from "./BaseChar.vue";
const props = defineProps({
  text: String,
  ctrl: String,
  show:Boolean
});
const emit = defineEmits(["touch"]);

const format_str = (str, len) => {
  let pre = new Array(len).fill(0).join("");
  str = pre + str;
  return str.slice(-len);
};

const cs = computed(() => {
  let s = parseInt(props.ctrl, 16).toString(2);
  return format_str(s, props.text.length);
});
</script>
