<template>
  <div class="clock" @click="update">
    <BaseLine
      v-for="(t, i) in text_list"
      :key="i"
      :text="t"
      :ctrl="ctrl_list[i]"
      :show="show"
      @touch="update"
    />
  </div>
</template>

<script setup>
import { onMounted, ref } from "vue";
import BaseLine from "./BaseLine.vue";

let text_list = `ITLISASAMPM
ACQUARTERDC
TWENTYFIVEX
HALFBTENFTO
PASTERUNINE
ONESIXTHREE
FOURFIVETWO
EIGHTELEVEN
SEVENTWELVE
TENSEOCLOCK`.split("\n");

let row = text_list.length;
const ctrl_list = ref([]);

const show = ref(false);
let hide_time = 0;
let last_time = 5000;

const clear = () => {
  show.value = false;
  ctrl_list.value = new Array(row).fill("0");
};

onMounted(() => {
  clear();
  setInterval(() => {
    if (show.value && new Date().getTime() >= hide_time) clear();
  }, 1000);
});

const hex_add = (hex1, hex2) => {
  let t = parseInt(hex1, 16) + parseInt(hex2, 16);
  return t.toString(16);
};

const list_add = (list, row, add) => {
  list[row] = hex_add(list[row], add);
};

const set_minute = (list, m) => {
  if (m == 0) list_add(list, 9, "3f");
  else if (m == 5) list_add(list, 2, "1e");
  else if (m == 10) list_add(list, 3, "38");
  else if (m == 15) list_add(list, 1, "5fc");
  else if (m == 20) list_add(list, 2, "7e0");
  else if (m == 25) list_add(list, 2, "7fe");
  else list_add(list, 3, "780");
};

const set_hour = (list, h) => {
  if (h == 0) list_add(list, 8, "3f");
  else if (h == 1) list_add(list, 5, "700");
  else if (h == 2) list_add(list, 6, "7");
  else if (h == 3) list_add(list, 5, "1f");
  else if (h == 4) list_add(list, 6, "780");
  else if (h == 5) list_add(list, 6, "78");
  else if (h == 6) list_add(list, 5, "e0");
  else if (h == 7) list_add(list, 8, "7c0");
  else if (h == 8) list_add(list, 7, "7c0");
  else if (h == 9) list_add(list, 4, "f");
  else if (h == 10) list_add(list, 9, "700");
  else list_add(list, 7, "3f");
};

const set_to = (list) => {
  list_add(list, 3, "3");
};

const set_past = (list) => {
  list_add(list, 4, "780");
};

const set_ampm = (list, am) => {
  if (am) list_add(list, 0, "6cc");
  else list_add(list, 0, "6c3");
};

const update = () => {
  let date = new Date();
  hide_time = date.getTime() + last_time;

  if (show.value) return;
  else show.value = true;

  let h = date.getHours();
  let m = date.getMinutes();

  let list = new Array(row).fill("0");

  // minute
  m = Math.floor(m / 5) * 5;

  if (m >= 35) {
    set_to(list);
    h = (h + 1) % 24;
    m = 60 - m;
  } else if (m >= 5) {
    set_past(list);
  }

  set_minute(list, m);

  // am/pm
  set_ampm(list, h < 12);

  // hour
  set_hour(list, h % 12);

  ctrl_list.value = list;
};
</script>

<style>
.clock {
  display: grid;
  height: 100%;
  grid: repeat(10, auto) / repeat(11, auto);
  align-content: center;
  justify-content: center;
  gap: 0.6vw;

  font-size: 16px;
  font-family: Consolas, "Courier New", Courier, monospace;
}
</style>

